// mixins star
@import 'mixins.scss';
// mixins end
// variables start
@import '_variables.scss';
//variables end
/*TYPOGRAPHY STYLE*/
body {
  font-family: $source;
  background: $body_bg;
  color: $main_color;
  position: relative;
  min-height: 100%;
  margin: 0 auto;
  min-width: 320px;
  font-size: 16px;
  line-height: 20px;
  @include clearfix;
}
h1, h2, h3, h4, h5, h6 {
  color:#000;
  padding: 0;
}
h1 {
  font-weight: normal;
  padding: 0;
  font-size: 30px;
  margin: 0 0 10px 0;
  text-transform: uppercase;
}
h2 {
  font-size:24px;
  line-height:1.3;
  margin: 0 0 10px 0;
}
h3 {
  font-size:18px;
  line-height:1.25;
  padding-bottom:15px;
}
h4 {
  font-size:18px;
  line-height:1.25;
}
h5 {
  font-size:16px;
  line-height:1.3;
}
h6 {
  font-size:14px;
  line-height:1.4;
}
hr {
  overflow:hidden;
  border:0;
  border-bottom:1px solid #000;
  margin-bottom:18px;
}
a {
  color:#fff;
  text-decoration:underline;
}
a:hover {
  text-decoration:none;
}
a:active {}
a:visited {}

/*BASIC BLOCKS MARKUP*/
.wrapper {
  min-width: 320px;
  position: relative;
  width: 100%;
  height: 100%;
}
.content {
  @include adaptiveAbsolutePosition(100%, auto, 320px, 940px, auto, 1200px);
  @include boxsizing;
}
