/*RESET*/
*,
:focus {
  outline: none; }

body {
  -webkit-fontype-smalloothing: antialiased; }

body,
html {
  height: 100%;
  width: 100%; }

a,
input[type=radio],
input[type=checkbox],
select {
  cursor: pointer; }

select[disabled] {
  cursor: default; }

/*normalization mozila*/
input:-webkit-autofill {
  background-color: transparent; }

/*normalization ie7*/
* + html input[type=checkbox],
* + html input[type=radio] {
  width: 13px;
  height: 13px; }

textarea {
  resize: none;
  vertical-align: top; }

em,
i {
  font-style: italic; }

img {
  vertical-align: top; }

input {
  vertical-align: middle; }

dl,
menu,
ol,
ul {
  margin: 1em 0; }

dd {
  margin: 0 0 0 40px; }

menu,
ol,
ul {
  padding: 0 0 0 40px; }

nav ul,
nav ol {
  list-style: none outside none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

q {
  quotes: "\201C" "\201D" "\2018" "\2019"; }

:focus {
  -moz-outline: 3px solid #fff !important; }

input[type="radio"]:focus {
  -moz-outline-radius: 12px !important;
  -moz-outline-offset: 0 !important; }

input[type="checkbox"]:focus {
  -moz-outline-offset: -1px !important;
  -moz-outline: 1px solid #000 !important; }

/*fix button click opera old version*/
button:after {
  content: ""; }

del {
  text-decoration: line-through; }

table {
  width: 100%; }

form {
  margin: 0;
  padding: 0; }

input::-ms-clear {
  display: none; }

/*RESET END*/
/*--reset end--*/
/*--typography start---*/
/*TYPOGRAPHY STYLE*/
body {
  font-family: "Source Sans Pro", "arial" sans-serif;
  background: #1b7e5a;
  color: #383838;
  position: relative;
  min-height: 100%;
  margin: 0 auto;
  min-width: 320px;
  font-size: 16px;
  line-height: 20px; }
  body:before, body:after {
    content: "";
    display: table; }
  body:after {
    clear: both; }

h1, h2, h3, h4, h5, h6 {
  color: #000;
  padding: 0; }

h1 {
  font-weight: normal;
  padding: 0;
  font-size: 30px;
  margin: 0 0 10px 0;
  text-transform: uppercase; }

h2 {
  font-size: 24px;
  line-height: 1.3;
  margin: 0 0 10px 0; }

h3 {
  font-size: 18px;
  line-height: 1.25;
  padding-bottom: 15px; }

h4 {
  font-size: 18px;
  line-height: 1.25; }

h5 {
  font-size: 16px;
  line-height: 1.3; }

h6 {
  font-size: 14px;
  line-height: 1.4; }

hr {
  overflow: hidden;
  border: 0;
  border-bottom: 1px solid #000;
  margin-bottom: 18px; }

a {
  color: #fff;
  text-decoration: underline; }

a:hover {
  text-decoration: none; }

/*BASIC BLOCKS MARKUP*/
.wrapper {
  min-width: 320px;
  position: relative;
  width: 100%;
  height: 100%; }

.content {
  height: auto;
  width: 100%;
  min-width: 320px;
  max-width: 940px;
  min-height: auto;
  max-height: 1200px;
  overflow: auto;
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

/*--typography end---*/
.content:before, .content:after {
  content: "";
  display: table; }

.content:after {
  clear: both; }

.main,
.sidebar {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

.main {
  width: 585px;
  padding: 30px;
  float: left;
  background: #ffffff; }

.sidebar {
  background: #232323;
  width: 355px;
  float: left; }

.nav_list {
  padding: 0;
  margin: 0; }
  .nav_list li {
    border-top: 1px solid #2e2e2e;
    transition: 0.5s linear; }
    .nav_list li:hover {
      background: #161616; }
  .nav_list .active {
    background: #161616; }
  .nav_list a {
    text-decoration: none;
    display: block;
    padding: 22px 0 22px 0; }
    .nav_list a:first-letter {
      text-transform: uppercase; }

.title_holder {
  border-bottom: 1px solid #ededed; }

.title {
  color: #e76e66;
  text-transform: none;
  font-weight: bold; }
  .title:first-letter {
    text-transform: uppercase; }

.sub_title {
  font-size: 22px; }
