// mixins star
@import 'mixins.scss';
// mixins end

// variables start
@import '_variables.scss';
//variables end

/*--fonts--*/
@import 'fonts.scss';
/*--fonts end--*/

/*--reset start--*/
@import 'reset.scss';
/*--reset end--*/

/*--typography start---*/
@import 'typography.scss';
/*--typography end---*/

// variables start
@import 'mediaquery.scss';
.content {
  @include clearfix;
}
.main,
.sidebar {
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
}
.main {
  width: 585px;
  padding: 30px;
  float: left;
  background: $content_bg;
}
//variables end
.sidebar {
  background: $sidebar_bg;
  width: 355px;
  float: left;
  ul {
  }
}
.nav_list {
  padding: 0;
  margin: 0;
  li {
    @include borderStyle(1px 0 0 0, $sidebar_border);
    transition: 0.5s linear;
    &:hover {
      background: $sidebar_hover_bg;
    }
  }
  .active {
    background: $sidebar_hover_bg;
  }
  a {
    text-decoration: none;
    display: block;
    padding: 22px 0 22px 0;
    &:first-letter {
      text-transform: uppercase;
    }
  }
}
//about page
.title_holder {
  @include borderStyle(0 0 1px 0, $title_border);
}
.title {
  color: $title_color;
  text-transform: none;
  font-weight: bold;
  &:first-letter {
    text-transform: uppercase;
  }
}
.sub_title {
  font-size: $fs_lg;
}