// BASE MIXIN
@mixin base {
  overflow: hidden;
  position: relative;
}

// CLEARFIX MIXIN
@mixin clearfix {
  &:before,
  &:after {
    content: "";
    display: table;
  }
  &:after {
    clear: both;
  }
}

// BOXSIZING MIXIN
@mixin boxsizing {
  -webkit-box-sizing: border-box;
   -moz-box-sizing: border-box;
        box-sizing: border-box;
}

// INPUT STYLE MIXIN
@mixin inputStyle ($coordinates: 0 0 0 0){
  $top: nth($coordinates, 1);
  $right: nth($coordinates, 2);
  $bottom: nth($coordinates, 3);
  $left: nth($coordinates, 4);
  padding: $top $right $bottom $left;
  border-left: none;
  outline-style: none;
  -webkit-appearance:none;
}

// PLACEHOLDER MIXIN
@mixin inputPlaceholder($input-text-color) {
  &::-webkit-input-placeholder {color: $input-text-color;}
  &:-moz-placeholder {color: $input-text-color; opacity: 1;}
  &::-moz-placeholder {color: $input-text-color; opacity: 1;}
  &:-ms-input-placeholder {color: $input-text-color;}
  &:focus:-moz-placeholder {color:transparent; opacity: 1;}
  &:focus::-moz-placeholder {color:transparent; opacity: 1;}
  &:focus::-webkit-input-placeholder {color:transparent;}
  &:focus:-ms-input-placeholder {color:transparent;}
  &:focus::placeholder {color:transparent;}
}

// BORDER STYLE MIXIN
@mixin borderStyle ( $coordinates: 0 0 0 0, $color: #000000, $style: solid ) {
  $top: nth($coordinates, 1);
  $right: nth($coordinates, 2);
  $bottom: nth($coordinates, 3);
  $left: nth($coordinates, 4);
  @if not(unitless($top)) {
    border-top: $top $style $color;
  }
  @if not(unitless($right)) {
    border-right: $right $style $color;
  }
  @if not(unitless($bottom)) {
    border-bottom: $bottom $style $color;
  }
  @if not(unitless($left)) {
    border-left: $left $style $color;
  }
}

// DISPLAY INLINE-BLOCK IE MIXIN
@mixin ie {
  zoom: 1;
  *display: inline;
}

//INLINE BLOCK MIXIN
@mixin inline {
  display: inline-block;
  vertical-align: top;
}

// TEXT-OVERFLOW MIXIN
@mixin textOverflow {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

// SIZE MIXIN
@mixin size($width, $height: $width) {
  width: $width;
  height: $height;
}

// LINK MIXIN
// @include linx(white, blue, green, red);
@mixin links ($link, $visit, $hover, $active) {
  color: $link;
  &:hover {
    color: $visit;
  }
  &:active {
    color: $hover;
  }
  &:visited {
    color: $active;
  }
}

// ANIMATED HOVER MIXIN
// @include animated-border;
@mixin animated-border {
  &:after {
    position: absolute;
    content:'';
    height: 1px;
    left: 0;
    right: 0;
    bottom: -5px;
  }
  &:hover {
    opacity: 1;
  }
  &:before {
    position: absolute;
    content:'';
    height: 1px;
    background: #fe4852;
    left: 0;
    width: 0%;
    bottom: -4px;
    transition: width .3s ease-in-out;
  }
  &:hover {
    &:before {
      width: 100%;
    }
  }
}

// TRANSITION MIXIN
// @include transition (color .3s ease);
@mixin transition($args...) {
  -webkit-transition: $args;
  -moz-transition: $args;
  -ms-transition: $args;
  -o-transition: $args;
  transition: $args;
}

// ANIMATE MIXIN
// @include keyframes(slide-down) {
//   0% { opacity: 1; }
//   90% { opacity: 0; }
// }
@mixin keyframes($animation-name) {
  @-webkit-keyframes $animation-name {
    @content;
  }
  @-moz-keyframes $animation-name {
    @content;
  }  
  @-ms-keyframes $animation-name {
    @content;
  }
  @-o-keyframes $animation-name {
    @content;
  }  
  @keyframes $animation-name {
    @content;
  }
}
// @include animation('slide-down 5s 3');
@mixin animation($str) {
  -webkit-animation: #{$str};
  -moz-animation: #{$str};
  -ms-animation: #{$str};
  -o-animation: #{$str};
  animation: #{$str};
}

// ABSOLUTE POSITION MIXIN
@mixin absolutePosition($width, $height) {
  height: $height;
  width: $width;
  position: absolute;
  top: 50%;
  left: 50%;
  $margin_left: -($width / 2);
  $margin_top: -($height / 2);
  margin: $margin_top 0 0 $margin_left;
}

// ABSOLUTE ADAPTIVE POSITION MIXIN
@mixin adaptiveAbsolutePosition($width, $height, $minWidth, $maxWidth, $minHeight, $maxHeight) {
  height: $height;
  width: $width;
  min-width: $minWidth;
  max-width: $maxWidth;
  min-height: $minHeight;
  max-height: $maxHeight;
  overflow: auto;
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}


// ALL BORDER RADIUS
@mixin borderRadius($radius) {
  -webkit-border-radius: $radius;
  border-radius: $radius;
  background-clip: padding-box;
}

//CUSTOM BORDER RADIUS MIXIN
@mixin customBorderRadius ( $radius: 0 0 0 0) {
  $top_left: nth($radius, 1);
  $top_right: nth($radius, 2);
  $bottom_right: nth($radius, 3);
  $bottom_left: nth($radius, 4);
  @if not(unitless($top_left)) {
    border-top-right-radius: $top_left;
  }
  @if not(unitless($top_right)) {
    border-top-left-radius: $right;
  }
  @if not(unitless($bottom_right)) {
    border-bottom-right-radius: $bottom_right;
  }
  @if not(unitless($bottom_left)) {
    border-bottom-left-radius: $bottom_left;
  }
  background-clip: padding-box;
}

// ITEM BEFORE
@mixin itemBefore ($width, $height, $bg) {
 &:before {
    position: absolute;
    content:'';
    height: $height;
    background: $bg;
    width: $width;
  }
}

//ITEM BEFORE POSITION
@mixin beforePosition ($position: 0 0 0 0) {
  &:before {
    $top: nth($position, 1);
    $right: nth($position, 2);
    $bottom: nth($position, 3);
    $left: nth($position, 4);
    @if not(unitless($top)) {
      top: $top;
    }
    @if not(unitless($right)) {
      right: $right;
    }
    @if not(unitless($bottom)) {
      bottom: $bottom;
    }
    @if not(unitless($left)) {
      left: $left;
    }
  }
}

// ITEM AFTER
@mixin itemAfter ($width, $height, $bg) {
 &:after {
    position: absolute;
    content:'';
    height: $height;
    background: $bg;
    width: $width;
  }
}

//ITEM BEFORE POSITION
@mixin afterPosition ($position: 0 0 0 0) {
  &:after {
    $top: nth($position, 1);
    $right: nth($position, 2);
    $bottom: nth($position, 3);
    $left: nth($position, 4);
    @if not(unitless($top)) {
      top: $top;
    }
    @if not(unitless($right)) {
      right: $right;
    }
    @if not(unitless($bottom)) {
      bottom: $bottom;
    }
    @if not(unitless($left)) {
      left: $left;
    }
  }
}

// OPACITY MIXIN
@mixin opacity($opacity) {
  opacity: $opacity;
  $opacity-ie: $opacity * 100;
  filter: alpha(opacity=$opacity-ie);
}